import React, { useState } from 'react';
import loadable from '@loadable/component';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Layout from '@components/layout';
import Section from '@components/section';
import Navbar from '@components/sections/navbar';
import Mainvisual from '@components/sections/mainvisual';
import DarumaIcon from '@components/svgs/daruma-icon';
import defaultData from '@components/defaultData';

import {
  HandshakeIcon,
  WrapTheHouseIcon,
  RentTheHouseIcon,
  CleanTheHouseIcon,
  WrapTheHeartIcon
} from '@components/svgs/service-icons';

const serviceIcons = [
  HandshakeIcon,
  WrapTheHouseIcon,
  RentTheHouseIcon,
  CleanTheHouseIcon,
  WrapTheHeartIcon
];

const getData = sectionName => defaultData.sections[sectionName].contents;

const Company = loadable(() => import('../components/sections/company'));
const Contact = loadable(() => import('../components/sections/contact'));
const Footer = loadable(() => import('../components/footer'));

const IndexPage = ({ location }) => {
  const [aboutData] = useState(getData('about'));
  const [serviceData] = useState(getData('service'));
  const [companyData] = useState(getData('company'));

  return (
    <Layout location={location}>
      <Navbar />
      <Mainvisual />
      <Section id='about'>
        <Grid container justifyContent='space-evenly' spacing={4}>
          <Grid item xs={12} md={4} lg={6}>
            <Box
              display='flex'
              alignItems='center'
              justifyContent='center'
              sx={{ height: '100%' }}
            >
              <Box
                sx={{
                  width: 180
                }}
              >
                <DarumaIcon />
              </Box>
            </Box>
          </Grid>
          <Grid item xs={12} md={8} lg={6}>
            <Box>
              <Typography
                variant='h5'
                letterSpacing={1.5}
                dangerouslySetInnerHTML={{
                  __html: aboutData[0].title
                }}
              ></Typography>
              <Typography
                letterSpacing={1.5}
                sx={{
                  mt: 3,
                  lineHeight: 1.8
                }}
              >
                {aboutData[0].description}
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </Section>
      <Section id='service'>
        <div className='service-background'>
          <div className='row'>
            {serviceData.map(({ color, title, description }, i) => {
              const ServiceIcon = serviceIcons[i];

              return (
                <div key={i} className='col-lg-6'>
                  <div
                    className={`mb-5 p-4 rounded service-item-${i + 1}`}
                    style={{
                      borderStyle: 'solid',
                      borderWidth: '2px',
                      borderColor: '#e9e9e9',
                      boxSizing: 'border-box',
                      letterSpacing: '1.4px',
                      lineHeight: '1.8rem'
                    }}
                  >
                    <div
                      className='row'
                      style={{
                        minHeight: '120px'
                      }}
                    >
                      <div className='col-9'>
                        <Typography mb={4} fontSize={14} color='#fff'>
                          <Box component='span' p={0.5} bgcolor={color}>
                            {title}
                          </Box>
                        </Typography>
                        <Typography>{description}</Typography>
                      </div>
                      <div className='col-3 d-flex align-items-center'>
                        <ServiceIcon
                          className='d-block mx-auto my-0'
                          style={{
                            stroke: color,
                            width: '100%',
                            maxWidth: '80px',
                            maxHeight: '80px'
                          }}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </Section>
      <Company data={companyData} />
      <Contact />
      <Footer />
    </Layout>
  );
};

export default IndexPage;
