import React from 'react';
import defaultData from './defaultData';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

const Section = ({ id, children }) => (
  <Box
    component='section'
    id={id}
    sx={{
      backgroundColor: id !== 'contact' && '#fff',
      py: 12
    }}
  >
    <Box
      className={`container${id === 'contact' ? ' bg-white px-sm-5 py-5' : ''}`}
    >
      <Box mb={5}>
        <Typography variant='h2'>{defaultData.sections[id].title}</Typography>
        <Typography align='center' mt={0.5}>
          {defaultData.sections[id].title_ja}
        </Typography>
      </Box>
      {children}
    </Box>
  </Box>
);

export default Section;
